export const GET_FIGHT = async ({ state }, fightId) => {
  const SentzNFT = state.drizzle.drizzleInstance.contracts.SentzNFT;

  let fight = await SentzNFT
    .methods
    .getFightHistory(fightId)
    .call({ from: state.accounts.activeAccount });

  return fight;
};

