import Vue from 'vue';

Vue.filter('pad', function (value, padding) {
    if (!value) return String(0).padStart(padding, '0');
    return String(value).padStart(padding, '0');
});

Vue.filter('number', function (value) {
    value = parseInt(value, 10);
    return value.toLocaleString(undefined, { minimumFractionDigits: 2 });
});

Vue.filter('token', function (value, precision) {
    if (!value) return value;
    const factor = Math.pow(10, precision);

    value = Math.round((value / factor) * 100) / 100;
    return value.toLocaleString(undefined, { minimumFractionDigits: 2 });
});

Vue.filter('address', function (value) {
    if (!value) return value;
    
    return value.substr(0, 5) + '...' + value.substr(-7);
});