import Vue from 'vue';
import Router from 'vue-router';
import Home from './pages/Home.vue';
import ShopSentz from './pages/ShopSentz.vue';
import ShopStonez from './pages/ShopStonez.vue';
import Play from './pages/Play.vue';
import Fight from './pages/Fight.vue';
import Marketplace from './pages/Marketplace.vue';
import Docs from './pages/Docs.vue';

Vue.use(Router);

export default new Router({
  mode: 'history',
  base: "/",
  routes: [
    {
      path: '/',
      name: 'home',
      component: Home
    },
    {
      path: '/shop',
      name: 'shop.sentz',
      component: ShopSentz
    },
    {
      path: '/shop/stonez',
      name: 'shop.stonez',
      component: ShopStonez
    },
    {
      path: '/play',
      name: 'play',
      component: Play
    },
    {
      path: '/play/fight/:tokenId',
      name: 'fight',
      component: Fight
    },
    {
      path: '/marketplace',
      name: 'marketplace',
      component: Marketplace
    },
    {
      path: '/help',
      name: 'help',
      component: Docs
    },
  ]
});
