<template>
  <div class="d-grid mb-1">
    <button @click.prevent="approve" class="btn btn-block btn-primary" type="button">Approve</button>
  </div>
</template>

<script>
import { mapState, mapGetters, mapActions } from 'vuex'

export default {
  computed: {
    ...mapState(['SentzAddress']),
    ...mapGetters('accounts', ['activeAccount']),
    ...mapGetters('drizzle', ['drizzleInstance']),
    
    contracts() {
      return this.drizzleInstance.contracts;
    }
  },

  data: () => ({}),

  methods: {
    ...mapActions(['SET_MAIN_STATE']),

    async approve() {
      await this.contracts.SoulBEP20
        .methods
        .approve(this.SentzAddress, this._getMaxApprove())
        .send({ from: this.activeAccount });
      
      this.SET_MAIN_STATE();
    },

    _getMaxApprove() {
      return window.BigInt(100000000000000*10**18);
    }
  }
}
</script>

