<script>
import CGHeader from '../views/CGHeader.vue';
import { mapState, mapGetters, mapActions } from 'vuex'

export default {
  components: { CGHeader },

  computed: {
    ...mapState(['pendingRewards']),
    ...mapGetters('accounts', ['activeAccount']),
    ...mapGetters('drizzle', ['drizzleInstance']),
    
    contracts() {
      return this.drizzleInstance.contracts;
    },

    enemyModel() {
      return this.enemyModels[(Math.floor(Math.random() * 3) + 1) - 1];
    },
  },

  data: () => ({
    tokenId: 0,
    SentzElementz: ['EARTH', 'WATER', 'FIRE', 'WIND'],
    SentzClass: ['BASIC', 'CRYSTAL', 'SHADOW', 'HOLY'],
    models: {
      EARTH: 'daf953dc804f434783fb83bee54acecc',
      WATER: 'ce6f067a5edd485685b593ee032be9b4',
      FIRE: '64915d2f22354b8daba6191d5e3b1e20',
      WIND: 'fb223435d1ec4078b0d856835d2704ea',
    },
    enemyModels: [
      {
        name: 'Forest Demon',
        model: 'e940d1681e434bb3ae70d1d36c1670c9',
      },
      {
        name: 'Ogre Cyclops',
        model: 'ad6e70dc4e944d83805c64a17ee20467',
      },
      {
        name: 'Familiar Imp',
        model: '7f43fae5157743cdb20b7aecf982492f',
      },
    ],
    activeSentz: {},
    duelId: 0,
    duelMatch: {},
    fightCampaignIsActive: false,
  }),

  beforeRouteEnter(to, from, next) {
    next(async (vm) => {
      vm.tokenId = parseInt(vm.$route.params.tokenId, 10);
      vm.activeSentz = await vm.GET_SINGLE_SENTZ(vm.tokenId);
      
      if (parseInt(vm.activeSentz.level, 10))
        next();
      else
        vm.$router.push('/play');
    })
  },

  async mounted() {
    this.tokenId = parseInt(this.$route.params.tokenId, 10);

    this.activeSentz = await this.GET_SINGLE_SENTZ(this.tokenId);
    this.GET_PENDING_REWARDS();

    this.$drizzleEvents.$on('drizzle/contractEvent', (payload) => {
      const { contractName, eventName, data } = payload;
      this[`${contractName}on${eventName}`](data);
    })
  },

  beforeDestroy() {
    this.$drizzleEvents.$off('drizzle/contractEvent');
  },

  methods: {
    ...mapActions(['GET_SINGLE_SENTZ', 'FIGHT_CAMPAIGN', 'FIND_DUEL', 'FIGHT_DUEL', 'GET_FIGHT', 'GET_PENDING_REWARDS']),

    async SentzNFTonFightCampaign(data) {
      if (data.owner !== this.activeAccount || !this.fightCampaignIsActive) return;
      this.fightCampaignIsActive = false;
      this.showMatchResult(true, data.rewards);
      this.GET_PENDING_REWARDS();
    },

    async SentzNFTonFightDuel(data) {
      if (data.owner !== this.activeAccount || !this.duelId) return;
      let rewards = this.duelMatch.rewards;
      this.duelId = 0;
      this.duelMatch = {};
      this.GET_PENDING_REWARDS();
      this.showMatchResult(data.win, rewards);
    },

    async SentzNFTonFindDuel(data) {
      if (data.owner !== this.activeAccount) return;
      
      this.duelId = data.fightId;
      this.duelMatch = await this.GET_FIGHT(data.fightId);
    },

    async fightCampaign() {
      this.fightCampaignIsActive = true;
      await this.FIGHT_CAMPAIGN(this.tokenId);
    },

    async findDuel() {
      await this.FIND_DUEL(this.tokenId);
    },

    async fightDuel() {
      await this.FIGHT_DUEL(this.duelId);
    },

    claimRewards() {
      alert('Coming Soon..');
    },

    // show modal with match result and rewards
    showMatchResult(win, rewards) {
      alert(`
        Match Result: ${win ? 'WIN' : 'LOST'}
        Rewards: ${rewards}
      `);
    }
  }
}
</script>

