<script>
import CGHeader from '../views/CGHeader.vue';
import { mapState, mapGetters, mapActions } from 'vuex'

export default {
  components: { CGHeader },

  computed: {
    ...mapState(['ownerSentz', 'sentz']),
    ...mapGetters('accounts', ['activeAccount']),
    ...mapGetters('drizzle', ['drizzleInstance']),
    
    contracts() {
      return this.drizzleInstance.contracts;
    }
  },

  data: () => ({
    SentzElementz: ['EARTH', 'WATER', 'FIRE', 'WIND'],
    models: {
      EARTH: 'daf953dc804f434783fb83bee54acecc',
      WATER: 'ce6f067a5edd485685b593ee032be9b4',
