      WIND: 'fb223435d1ec4078b0d856835d2704ea',
    }
  }),

  created() {
    // fetch blockchain data needed for this page
    this.SET_SENTZ();
  },

  methods: {
    ...mapActions(['SET_SENTZ', 'REFRESH_SENTZ']),

    refreshSentz() {
      this.REFRESH_SENTZ();
      this.SET_SENTZ();
    }
  }
}
</script>

