<template>
  <div v-if="isDrizzleInitialized" id="app">
    <!-- route outlet -->
    <router-view></router-view>
  </div>

  <div v-else>Loading...</div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'

export default {
  name: 'app',

  computed: {
    ...mapGetters('drizzle', ['isDrizzleInitialized']),
  },

  watch: {
    isDrizzleInitialized: function (val) {
      if (val) {
        this.SET_MAIN_STATE();
      }
    }
  },

  methods: {
    ...mapActions(['SET_MAIN_STATE']),
  }
}
</script>

<style lang="scss">
  .container {
    max-width: 1400px;
  }

  @media (max-width: 767.98px) {
    .card-egg {
      width: 100% !important;
    }
  }
</style>
