export const SET_BALANCE = (state, balance) => (state.balance = balance);

export const SET_ALLOWANCE = (state, allowance) => (state.allowance = allowance);

export const SET_SPAWN_PRICE = (state, sentzSpawnPrice) => (state.sentzSpawnPrice = sentzSpawnPrice);

export const SET_OWNER_SENTZ = (state, ownerSentz) => (state.ownerSentz = ownerSentz);

export const SET_SENTZ = (state, { tokenId, sent }) => {
    state.sentz[tokenId] = sent;
};

export const SET_REFRESH_SENTZ = (state, refreshSentz) => (state.refreshSentz = refreshSentz);

export const SET_PENDING_REWARDS = (state, pendingRewards) => (state.pendingRewards = pendingRewards);