<script>
import Approve from '../views/Approve.vue';
import CGHeader from '../views/CGHeader.vue';
import ShopHeader from '../views/ShopHeader.vue';
import { mapState, mapGetters, mapActions } from 'vuex'

export default {
  components: { Approve, CGHeader, ShopHeader },

  computed: {
    ...mapState(['sentzSpawnPrice']),
    ...mapGetters(['isApproved']),
    ...mapGetters('accounts', ['activeAccount']),
    ...mapGetters('drizzle', ['drizzleInstance',]),

    contracts() {
      return this.drizzleInstance.contracts;
    }
  },

  data: () => ({
    SentzElements: ['EARTH', 'WATER', 'FIRE', 'WIND'],
    randomSentz: 3,
    quantity: {
      earth: 0,
      water: 0,
      fire: 0,
      wind: 0,
    }
  }),

  created() {
    this.SET_SPAWN_PRICE();
  },

  methods: {
    ...mapActions(['SET_MAIN_STATE', 'SET_SPAWN_PRICE', 'REFRESH_SENTZ']),

    spawnEarth() {
      this.spawn(this.quantity.earth, this.SentzElements.indexOf('EARTH'));
    },
    spawnWater() {
      this.spawn(this.quantity.water, this.SentzElements.indexOf('WATER'));
    },
    spawnFire() {
      this.spawn(this.quantity.fire, this.SentzElements.indexOf('FIRE'));
    },
    spawnWind() {
      this.spawn(this.quantity.wind, this.SentzElements.indexOf('WIND'));
    },

    async spawn(quantity, element) {
      let elements = [];
      for (let i = 0; i < quantity; i++) {
        elements.push(element);
      }

      await this.contracts.SentzNFT
        .methods
        .spawn(elements)
        .send({ from: this.activeAccount });

      this.quantity[this.SentzElements[element].toLowerCase()] = 0;

      this.SET_MAIN_STATE();
      this.REFRESH_SENTZ();
    },
  }
}
</script>

